.about{
	position: absolute;
	top: 15%;
	width: 100%;
	display: flex;
	flex-direction: column;
}

.about h1{
	margin-top: 70px;
	margin-left: 30px;
	margin-bottom: 5px;
	text-transform: capitalize;
	color: #6e6e6e;
}

.about hr{
	margin-left: 30px;
	position: relative;
	width: 200px;
	height: 1px;
	background-color: #009688;
	border: 0.2px solid #009688;
	border-radius: 50%;
}

.about-sec{
	margin: 30px;
	width: 90%;
	height: 60%;
	display: flex;
	justify-content: space-between;
}

.about-sec img{
	width: 40%;
}

.about-txt{
	flex-basis: 50%;
	display: flex;
	flex-direction: column;
}

@media only screen and (max-width: 500px) {

	.about h1{
		margin-top: 20px;
	}
	.about-sec {
	    flex-direction: column;
	}
	.about-sec img{
	  	width: 80%;
		flex-basis: 100%;
	}

}